import React from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import Layout from './Layout';
import Home from '../pages/Home';
import Login from '../pages/Login';
import Register from '../pages/Register';
import Profile from '../pages/Profile';
import Finder from '../pages/Finder';
import Math from '../pages/Math';
import Idiomas from '../pages/Idiomas'
import Oficios from '../pages/Oficios'
import Ciencias from '../pages/Ciencias'
import Leyes from '../pages/Leyes'
import Psicologia from '../pages/Psicologia'
import Robotica from '../pages/Robotica'
import Nutricion from '../pages/Nutricion'
import Fitness from '../pages/Fitness'
import Desarrollo from '../pages/Desarrollo'
import NotFound from '../pages/NotFound';

function App() {
    return (
        <BrowserRouter>
            <Layout>
                <Switch>
                    <Route exact path='/janz' component={Home} />
                    <Route exact path="/Login" component={Login} />
                    <Route exact path="/Register" component={Register} />
                    <Route exact path="/Profile" component={Profile} />
                    <Route exact path="/Finder" component={Finder} />
                    <Route exact path="/Math" component={Math} />
                    <Route exact path="/Idiomas" component={Idiomas} />
                    <Route exact path="/Ciencias" component={Ciencias} />
                    <Route exact path="/Oficios" component={Oficios} />
                    <Route exact path="/Leyes" component={Leyes} />
                    <Route exact path="/Psicologia" component={Psicologia} />
                    <Route exact path="/Robotica" component={Robotica} />
                    <Route exact path="/Nutricion" component={Nutricion} />
                    <Route exact path="/Fitness" component={Fitness} />
                    <Route exact path="/Desarrollo" component={Desarrollo} />
                    <Route component={NotFound} />
                </Switch>
            </Layout>
        </BrowserRouter>
    )
}

export default App;