import React from 'react'
import video from '../assets/video/video.mp4'
import '../pages/Profile'

class CardProfile extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="container cardProfile">

                    <div className="row text-center">
                        
                        <div className="col-xs-1-12 col-lg-12">
                            <video className='cardProfile--video' src={video} controls></video>
                        </div>

                        <div className='row'>
                            <div className='col-xs-1-12 col-lg-12 cardProfile--desc'>
                                <p>Hi! my name is Alejandro Zepeda Alvarez, I am an engineer in computer systems, I have tried to always stay at the forefront of new technologies throughout my career, through courses and certifications and of course putting it into practice, I like to be proactive and I consider myself self-taught definitely.</p>
                            </div>
                        </div>
                        
                    </div>                
                    
                </div>
            </React.Fragment>
        )
    }
}

export default CardProfile