import React from 'react'
import idiomas from '../assets/img/idiomas.png'
import leyes from '../assets/img/leyes.png'
import mat from '../assets/img/matematicas.png'
import oficios from '../assets/img/oficios.png'
import psicologia from '../assets/img/psicologia.png'
import robotica from '../assets/img/robotica.png'
import fitness from '../assets/img/fitness.png'
import ciencias from '../assets/img/ciencias.png'
import nutricion from '../assets/img/nutricion.png'
import desarrollo from '../assets/img/desarrollo.png'
import {Link} from 'react-router-dom'

class BadgeHero extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="container badgeSubjects">
                    <div className="row justify-content-center text-center">
                        <h2>Quiero asesoria en:</h2>
                    </div>
                    <div className="row justify-content-center text-center">
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Idiomas'> 
                                <img src={idiomas} alt="Idiomas" />
                                <h6>Idiomas</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Ciencias'>
                                <img src={ciencias} alt="Ciencias" />
                                <h6>Ciencias</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Math'>
                                <img src={mat} alt="Matemáticas" />
                                <h6>Matemáticas</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Leyes'>
                                <img src={leyes} alt="Leyes" />
                                <h6>Leyes</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='Psicologia'>
                                <img src={psicologia} alt="Psicología" />
                                <h6>Psicología</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Robotica'>
                                <img src={robotica} alt="Robótica" />
                                <h6>Robótica</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Nutricion'>
                                <img src={nutricion} alt="Nutrición" />
                                <h6>Nutrición</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Fitness'>
                                <img src={fitness} alt="Fitness" />
                                <h6>Fitness</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Desarrollo'>
                                <img src={desarrollo} alt="Desarrollo" />
                                <h6>Desarrollo</h6>
                            </Link>
                        </div>
                        <div className="col-xs-1 badgeSubjects--item">
                            <Link to='/Oficios'>
                            <img src={oficios} alt="Oficios" />
                            <h6>Oficios</h6>
                            </Link>
                        </div>
                        <div className="row justify-content-center text-center badgeSubjects--parrafo">
                        <p>Con Janz aprende desde tu casa, te ofrecemos asesorias con maestros certificados y profesionales, puedes aprender cualquier materia desde nivel Preescolar hasta Doctorado, en Janz puedes seleccionar un tema en especifico y seguro estara un asesor disponible para ayudarte a comprenderlo mejor, o si lo prefieres puedes agendar tu asesoria. </p>
                    </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default BadgeHero