import React from 'react'
import logo from '../assets/img/janz.png'
import user from '../assets/img/UserProfile (1).png'
import CardProfile from './CardProfile'
import book from '../assets/img/book.png'
import birrete from '../assets/img/birrete.png'
import mexico from '../assets/img/mexico.png'
import tarifa from '../assets/img/tarifa.png'
import star from '../assets/img/star.png'



class Profile extends React.Component {
    render() {
        return (
            <React.Fragment>
                    
                <div class="container profile">

                    <div className='row text-center'>

                        <div className='col-xs-1-12 col-lg-6 profile--header'>
                            <img className='profile--logo' src={logo} alt="" />
                        </div>
                        <div className='col-xs-1-12 col-lg-6 profile--header'>
                            <p className='profile--name'>Alex Zepeda</p>
                        </div>

                    </div>

                    <div class="row text-center">

                        <div class="col-xs-1-12 col-lg-6 profile--face">
                            
                            <div className='profile--info'>
                                <img src={user} alt="" />
                                <p>@AlexGeek</p>
                                <p>Full Stack Dev</p>                                
                                <p>Facebook / Twitter</p>
                            </div>                         
                        
                        
                    
                            <div className="row justify-content-center">

                                <div className="col-xs-1-12 col-lg-2 card--jobTitle">
                                    <div>
                                        <img className='card--book' src={birrete} alt=""/>
                                        <p>Enseña Desarrollo</p>
                                    </div>
                                </div>
                                <div className="col-xs-1-12 col-lg-2 card--speciality">
                                    <img className='card--birrete' src={book} alt=""/>
                                        <p>Ingeniero en Sistemas</p>
                                    </div>
                                <div className="col-xs-1-12 col-lg-2 card--region">
                                    <img className='card--birrete' src={mexico} alt=""/>
                                    <p>De México</p>
                                </div>
                                <div className="col-xs-1-12 col-lg-2 card--tarifa">
                                    <img className='card--birrete' src={tarifa} alt=""/>
                                    <p>Tarífa</p>
                                </div> 
                                <div className="col-xs-1-12 col-lg-2 card--tarifa">
                                    <img className='card--birrete' src={star} alt=""/>
                                    <p>Calificación</p>
                                </div> 
                            </div>
                        </div>

                        <div class="col-xs-1-12 col-lg-6 profile--post">
                            <CardProfile/>
                        </div>
                        
                    </div>
                    
                    </div>
            </React.Fragment>
        );
    }
}

export default Profile;