import React from 'react'
import scratch from '../assets/img/scratch degradado.png'
import person1 from '../assets/img/person2.png'
import LogoAnimation from '../components/LogoAnimation'


class Hero extends React.Component {

    // constructor() {
    //     super()
    //     this.person=React.createRef()
    // }

    // appear() {
    //     setTimeout(() => {
    //         this.person
    //     }, 2000);
    // }

    render() {
        return (
            <React.Fragment>
                <section className='hero'>

                    <div className="container hero--container">

                        <div className="row">                           


                            <div className="col-xs-1-12 col-lg-6 hero--container__welcome">
                                <h1 className='hero--container__title'>Nosotros podemos enseñarte!</h1>
                                <p className='hero--container__text'>Estas listo para aprender?</p>
                            </div>

                            <div className="col-xs-1-12 col-lg-6 hero--container__imgs">
                                <img src={scratch} className="img-fluid position-absolute hero--container__scratch" alt="Hero"/>
                                <img src={scratch} className="img-fluid position-absolute hero--container__scratch" alt="Hero"/>
                                <img ref = { this.person } src={person1} className="img-fluid position-absolute hero--container__person" alt="Persona"/>
                            </div>
                            
                        </div>

                        <div>
                        <LogoAnimation/>
                        </div>
                        

                    </div>
                </section>
            </React.Fragment>
        );
    }
}

export default Hero;