import React from 'react'
import logo from '../assets/img/janz.png'
import { Link } from 'react-router-dom';


class Header extends React.Component {
    render() {
        return (
           <div className="container-fluid header sticky-top">
               <div className="row align-items-center ">
                    <div className="col-xs-1-12 col-lg-8 header--nav">
                        <Link to='/janz'>
                            <img className='header--logo' src={logo} alt="logo janz"/>
                        </Link>
                    </div>
                    <div className="col-xs-1-12 col-lg-1 header--nav text-center">
                        <Link to="/Login">
                           <span className="font-weight-light">Iniciar Sesión</span>
                        </Link>
                    </div>
                    <div className="col-xs-1-12 col-lg-1 header--nav text-center">
                        <Link to="/Finder">
                           <span className="font-weight-light">Encuentra un Profesor</span>
                        </Link>
                    </div> 
                    <div className="col-xs-1-12 col-lg-1 header--nav text-center">
                        <Link to="/Register">
                           <button className='header--btn'>Regístrate</button>
                        </Link>
                    </div>
               </div>
           </div>
        );
    }
}

export default Header;