import React from 'react'
import profile from '../assets/img/UserProfile (2).png'
import book from '../assets/img/book.png'
import birrete from '../assets/img/birrete.png'
import mexico from '../assets/img/mexico.png'
import tarifa from '../assets/img/tarifa.png'
import star from '../assets/img/star.png'
import video from '../assets/video/video.mp4'
import '../pages/Profile'
import {Link} from 'react-router-dom'

class Card extends React.Component {
    render() {
        return (
            <React.Fragment>
                <div className="container card">

                    <div className="row text-center">

                        <div className='col-xs-1-12 card--profile'>
                            <img className='card--imgProfile' src= {profile} alt="Imagen Perfil"/>
                            <p className='card--name'>Alex Zepeda</p>
                            <p className='card--user'>@AlexGeek</p>
                        </div>
                        <div className='col-xs-1-12 col-lg-5 card--desc'>
                            <p>Hi! my name is Alejandro Zepeda Alvarez, I am an engineer in computer systems, I have tried to always stay at the forefront of new technologies throughout my career, through courses and certifications and of course putting it into practice, I like to be proactive and I consider myself self-taught definitely.</p>
                        </div>                        
                        <div className="col-xs-1-12">
                            <div>
                                <video className='card--video' src={video} controls></video>
                            </div>
                            <div className='col-xs-1-12'>
                                <button className='card--buttons'>Prueba Gratuita</button>
                                <Link to='/Profile'>
                                <button className='card--buttons'>Entrar al Perfil</button>
                                </Link>
                            </div>
                        </div>
                        
                    </div>
                
                    <div className="row justify-content-center">

                        <div className="col-xs-1-12 col-lg-2 card--jobTitle">
                            <div>
                                <img className='card--book' src={birrete} alt=""/>
                                <p>Enseña Desarrollo</p>
                            </div>
                        </div>
                        <div className="col-xs-1-12 col-lg-2 card--speciality">
                            <img className='card--birrete' src={book} alt=""/>
                                <p>Ingeniero en Sistemas</p>
                            </div>
                        <div className="col-xs-1-12 col-lg-2 card--region">
                            <img className='card--birrete' src={mexico} alt=""/>
                            <p>De México</p>
                        </div>
                        <div className="col-xs-1-12 col-lg-2 card--tarifa">
                            <img className='card--birrete' src={tarifa} alt=""/>
                            <p>Tarífa</p>
                        </div> 
                        <div className="col-xs-1-12 col-lg-2 card--tarifa">
                            <img className='card--birrete' src={star} alt=""/>
                            <p>Calificación</p>
                        </div>                        

                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Card