import React from 'react'
import profile from '../assets/img/UserProfile (5).png'
import birrete from '../assets/img/birrete.png'
import mexico from '../assets/img/mexico.png'
import age from '../assets/img/age.png'
import user from '../assets/img/user.png'
import '../pages/Profile'

class BadgeRegister extends React.Component {

    

    render() {
        return (
            <React.Fragment>
                <div className="container BadgeReg">

                    <div className="row text-center">

                        <div className='col-xs-1-12 col-lg-12 BadgeReg--profile'>
                            <img className='BadgeReg--imgProfile' src= {profile} alt="Imagen Perfil"/>
                            <i className='BadgeReg--name'>{this.props.firstName} </i>
                            <i className='BadgeReg--lastName'>{this.props.lastName} </i>
                        </div>
                        
                    </div>
                
                    <div className="row justify-content-center">

                        <div className="col-xs-1-12 col-lg-1-3 BadgeReg--jobTitle">
                            <div>
                                <img className='BadgeReg--book' src={birrete} alt=""/>
                                <p>{this.props.ocupation}</p>
                            </div>
                        </div>
                        <div className="col-xs-1-12 col-lg-1-3 BadgeReg--speciality">
                            <img className='BadgeReg--birrete' src={age} alt=""/>
                            <p>{this.props.age}</p>
                        </div>
                        <div className="col-xs-1-12 col-lg-1-3 BadgeReg--region">
                            <img className='BadgeReg--birrete' src={mexico} alt=""/>
                            <p>{this.props.country}</p>
                        </div>
                        <div className="col-xs-1-12 col-lg-1-3 BadgeReg--region">
                            <img className='BadgeReg--birrete' src={user} alt=""/>
                            <p>@{this.props.user}</p>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default BadgeRegister